import { API_BASE_URL } from '../../../../common/constants/constants';
import { http } from '../../../services';

export const requestGologinVaultSettings = async (requiredParamsString: string) => {
  const url = `${API_BASE_URL}/gologin-settings?${requiredParamsString}`;
  try {
    const data = await http(url, {
      method: 'GET'
    });
    return data.body;
  } catch (error) {
    throw error;
  }
};

export const updateGologinVaultSettings = async (options: any) => {
  const url = `${API_BASE_URL}/gologin-settings`;
  try {
    const data = await http(url, {
      method: 'PATCH',
      body: JSON.stringify(options),
    });
    return data.body;
  } catch (error) {
    throw error;
  }
};
