import { Switch } from 'antd';
import React, { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { requestGologinVaultSettings, updateGologinVaultSettings } from './api';
import { SwitchContainer } from './styles';
import LoadingPage from '../../common/loading-page';
import { TabWrapper } from '../menu/tab-wrapper';
import { PageTitle } from '../styles';

const PasswordManagerPage: FC = () => {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [isGologinVaultEnabled, setIsGologinVaultEnabled] = useState<boolean>(true);

  const { t: translation } = useTranslation();

  useEffect(() => {
    const requiredParams = 'gologinVault=true';
    requestGologinVaultSettings(requiredParams).then((res: any) => {
      const { gologinVault } = res;
      setIsGologinVaultEnabled(gologinVault.enabled);
      setIsLoading(false);
    });
  }, []);

  const setGologinVaultSetting = async (checked: boolean): Promise<void> => {
    const options = {
      gologinVault: {
        enabled: checked,
      },
    };

    await updateGologinVaultSettings(options);
    setIsGologinVaultEnabled(checked);
  };

  const getGologinVaultSettingsContent = (): JSX.Element => {
    if (isLoading) {
      return <LoadingPage />;
    }

    return (
      <SwitchContainer>
        <Switch
          checked={isGologinVaultEnabled}
          onChange={setGologinVaultSetting}
          style={{ marginRight: 10 }}
        />
        {translation('personalArea.vault.title')}
      </SwitchContainer>
    );
  };

  return (
    <TabWrapper tabKey='password-manager'>
      <PageTitle>
        {translation('personalArea.vault.passwordManagerSwitchText')}
      </PageTitle>
      {getGologinVaultSettingsContent()}
    </TabWrapper>
  );
};

export default PasswordManagerPage;
